/**
 * Web  Alert.alertreact-native-web 
 */
import React from 'react';
import {
  Modal,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';

export interface WebAlertButton {
  text: string;
  onPress?: () => void;
  style?: 'default' | 'cancel' | 'destructive';
}

interface WebAlertModalProps {
  visible: boolean;
  title: string;
  message?: string;
  buttons: WebAlertButton[];
  onClose: () => void;
}

const colors = {
  overlay: 'rgba(0,0,0,0.5)',
  card: '#fff',
  border: '#ddd',
  text: '#333',
  visaBlue: '#0033a0',
  danger: '#dc3545',
};

export function WebAlertModal({
  visible,
  title,
  message,
  buttons,
  onClose,
}: WebAlertModalProps) {
  const handlePress = (btn: WebAlertButton) => {
    btn.onPress?.();
    onClose();
  };

  if (!visible) return null;

  return (
    <Modal
      visible={visible}
      transparent
      animationType="fade"
      onRequestClose={onClose}
    >
      <TouchableOpacity
        style={styles.overlay}
        activeOpacity={1}
        onPress={onClose}
      >
        <View style={styles.box} onStartShouldSetResponder={() => true}>
          {title ? <Text style={styles.title}>{title}</Text> : null}
          {message ? <Text style={styles.message}>{message}</Text> : null}
          <View style={[styles.buttons, buttons.length > 2 && styles.buttonsColumn]}>
            {buttons.map((btn, i) => (
              <TouchableOpacity
                key={i}
                style={[
                  styles.btn,
                  buttons.length > 2 && styles.btnBlock,
                  buttons.length > 2 && i === buttons.length - 1 && styles.btnBlockLast,
                  btn.style === 'cancel' && styles.btnCancel,
                  btn.style === 'destructive' && styles.btnDestructive,
                ]}
                onPress={() => handlePress(btn)}
                activeOpacity={0.7}
              >
                <Text
                  style={[
                    styles.btnText,
                    btn.style === 'destructive' && styles.btnTextDanger,
                  ]}
                  numberOfLines={1}
                >
                  {btn.text}
                </Text>
              </TouchableOpacity>
            ))}
          </View>
        </View>
      </TouchableOpacity>
    </Modal>
  );
}

const styles = StyleSheet.create({
  overlay: {
    flex: 1,
    backgroundColor: colors.overlay,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 24,
  },
  box: {
    backgroundColor: colors.card,
    borderRadius: 14,
    padding: 20,
    minWidth: 280,
    maxWidth: '100%',
  },
  title: {
    fontSize: 17,
    fontWeight: '600',
    color: colors.text,
    marginBottom: 8,
  },
  message: {
    fontSize: 15,
    color: colors.text,
    marginBottom: 20,
  },
  buttons: {
    flexDirection: 'row',
    justifyContent: 'flex-end',
    flexWrap: 'wrap',
    gap: 10,
  },
  buttonsColumn: {
    flexDirection: 'column',
    marginTop: 4,
  },
  btn: {
    paddingVertical: 12,
    paddingHorizontal: 20,
    minHeight: 44,
    justifyContent: 'center',
  },
  btnBlock: {
    width: '100%',
    alignItems: 'center',
    borderBottomWidth: StyleSheet.hairlineWidth,
    borderBottomColor: colors.border,
  },
  btnBlockLast: {
    borderBottomWidth: 0,
  },
  btnCancel: {},
  btnDestructive: {},
  btnText: {
    fontSize: 16,
    color: colors.visaBlue,
    fontWeight: '600',
  },
  btnTextDanger: {
    color: colors.danger,
  },
});
