/**
 * ShopHelper - VISA Optimizer
 * @format
 */

import React, { useCallback, useEffect, useRef, useState } from 'react';
import {
  Alert,
  Platform,
  StatusBar,
  StyleSheet,
  Text,
  TouchableOpacity,
  useColorScheme,
  View,
} from 'react-native';
import { SafeAreaProvider, SafeAreaView } from 'react-native-safe-area-context';
import AsyncStorage from '@react-native-async-storage/async-storage';
import type { Currency, ConvertTo, Rates, StoredRates } from './currency';
import {
  DEFAULT_BASE_CURRENCY,
  DEFAULT_CONVERT_TO,
  fetchRates,
  isRatesFresh,
  parseBaseCurrency,
  parseConvertTo,
  STORAGE_BASE_CURRENCY,
  STORAGE_CONVERT_CURRENCY,
  STORAGE_RATES,
} from './currency';
import type { Lang } from './i18n/visaOptimizer';
import { visaOptimizerI18n } from './i18n/visaOptimizer';
import { IconFolder, IconList, IconSettings } from './components/TabIcons';
import { WebAlertModal } from './components/WebAlertModal';
import { LangSwitchScreen } from './LangSwitchScreen';
import { SavedListsScreen } from './SavedListsScreen';
import { SettingsScreen } from './SettingsScreen';
import { VisaOptimizer } from './VisaOptimizer';

const STORAGE_LANG = 'visa_optimizer_lang';

function App() {
  const isDarkMode = useColorScheme() === 'dark';
  const [activeTab, setActiveTab] = useState(0);
  const [lang, setLang] = useState<Lang>('ja');
  const [showLangPicker, setShowLangPicker] = useState(false);
  const [baseCurrency, setBaseCurrency] = useState<Currency>(DEFAULT_BASE_CURRENCY);
  const [convertCurrency, setConvertCurrency] = useState<ConvertTo>(DEFAULT_CONVERT_TO);
  const [rates, setRates] = useState<Rates | null>(null);
  const [webAlert, setWebAlert] = useState<{
    title: string;
    message?: string;
    buttons: Array<{ text: string; onPress?: () => void; style?: 'cancel' | 'destructive' }>;
  } | null>(null);
  const setWebAlertRef = useRef(setWebAlert);

  useEffect(() => {
    setWebAlertRef.current = setWebAlert;
  }, []);

  useEffect(() => {
    if (Platform.OS !== 'web') return;
    const original = Alert.alert;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    (Alert as any).alert = (
      title: string,
      message?: string,
      buttons?: Array<{ text?: string; onPress?: () => void; style?: string }>,
    ) => {
      setWebAlertRef.current({
        title,
        message,
        buttons: (buttons ?? [{ text: 'OK' }]).map((b) => ({
          text: b.text ?? 'OK',
          onPress: b.onPress,
          style: b.style as 'cancel' | 'destructive' | undefined,
        })),
      });
    };
    return () => {
      (Alert as any).alert = original;
    };
  }, []);

  const loadLang = useCallback(() => {
    AsyncStorage.getItem(STORAGE_LANG).then((l) => {
      if (l === 'zh' || l === 'zhCN' || l === 'ja' || l === 'en') setLang(l);
    });
  }, []);

  const loadCurrency = useCallback(() => {
    AsyncStorage.multiGet([STORAGE_BASE_CURRENCY, STORAGE_CONVERT_CURRENCY]).then(
      ([[, base], [, convert]]) => {
        setBaseCurrency(parseBaseCurrency(base));
        setConvertCurrency(parseConvertTo(convert));
      },
    );
  }, []);

  const loadRates = useCallback(() => {
    AsyncStorage.getItem(STORAGE_RATES).then((raw) => {
      if (raw) {
        try {
          const stored: StoredRates = JSON.parse(raw);
          if (isRatesFresh(stored.fetchedAt)) {
            setRates({ JPY_TO_TWD: stored.JPY_TO_TWD, JPY_TO_USD: stored.JPY_TO_USD });
          }
        } catch {}
      }
      fetchRates()
        .then((r) => {
          setRates(r);
          AsyncStorage.setItem(
            STORAGE_RATES,
            JSON.stringify({ ...r, fetchedAt: new Date().toISOString() }),
          );
        })
        .catch(() => {});
    });
  }, []);

  useEffect(() => {
    loadLang();
    loadCurrency();
    loadRates();
  }, []);

  useEffect(() => {
    loadLang();
  }, [activeTab]);

  const onConvertCurrencyChange = useCallback((c: ConvertTo) => {
    setConvertCurrency(c);
    AsyncStorage.setItem(STORAGE_CONVERT_CURRENCY, c);
  }, []);

  const onBaseCurrencyChange = useCallback(
    (c: Currency) => {
      setBaseCurrency(c);
      if (c === convertCurrency) onConvertCurrencyChange('none');
      AsyncStorage.setItem(STORAGE_BASE_CURRENCY, c);
    },
    [convertCurrency, onConvertCurrencyChange],
  );

  const t = visaOptimizerI18n[lang];

  const win =
    Platform.OS === 'web' && typeof globalThis !== 'undefined' && 'innerWidth' in globalThis
      ? (globalThis as unknown as { innerWidth: number; innerHeight: number })
      : null;
  const safeAreaInitialMetrics =
    win !== null
      ? {
          insets: { top: 0, left: 0, right: 0, bottom: 0 },
          frame: { x: 0, y: 0, width: win.innerWidth, height: win.innerHeight },
        }
      : undefined;

  return (
    <SafeAreaProvider initialMetrics={safeAreaInitialMetrics}>
      <StatusBar barStyle={isDarkMode ? 'light-content' : 'dark-content'} />
      <SafeAreaView style={styles.container} edges={['top', 'left', 'right', 'bottom']}>
        {showLangPicker ? (
          <LangSwitchScreen
            currentLang={lang}
            onBack={() => setShowLangPicker(false)}
            onConfirm={loadLang}
          />
        ) : (
          <>
            <View style={styles.content}>
              {activeTab === 0 ? (
                <VisaOptimizer
                  baseCurrency={baseCurrency}
                  convertCurrency={convertCurrency}
                  rates={rates}
                />
              ) : activeTab === 1 ? (
                <SavedListsScreen
                  baseCurrency={baseCurrency}
                  convertCurrency={convertCurrency}
                  rates={rates}
                />
              ) : (
                <SettingsScreen
                  lang={lang}
                  baseCurrency={baseCurrency}
                  convertCurrency={convertCurrency}
                  onBaseCurrencyChange={onBaseCurrencyChange}
                  onConvertCurrencyChange={onConvertCurrencyChange}
                  onOpenLangPicker={() => setShowLangPicker(true)}
                />
              )}
            </View>
            <View style={styles.tabBar}>
              <TouchableOpacity
                style={[styles.tab, activeTab === 0 && styles.tabActive]}
                onPress={() => setActiveTab(0)}
              >
                <IconList color={activeTab === 0 ? colors.visaBlue : colors.tabInactive} size={24} />
                <Text style={[styles.tabText, activeTab === 0 && styles.tabTextActive]}>
                  {t.tabList}
                </Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={[styles.tab, activeTab === 1 && styles.tabActive]}
                onPress={() => setActiveTab(1)}
              >
                <IconFolder color={activeTab === 1 ? colors.visaBlue : colors.tabInactive} size={24} />
                <Text style={[styles.tabText, activeTab === 1 && styles.tabTextActive]}>
                  {t.tabSaved}
                </Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={[styles.tab, activeTab === 2 && styles.tabActive]}
                onPress={() => setActiveTab(2)}
              >
                <IconSettings color={activeTab === 2 ? colors.visaBlue : colors.tabInactive} size={24} />
                <Text style={[styles.tabText, activeTab === 2 && styles.tabTextActive]}>
                  {t.tabSettings}
                </Text>
              </TouchableOpacity>
            </View>
          </>
        )}
      </SafeAreaView>
      {Platform.OS === 'web' && webAlert && (
        <WebAlertModal
          visible={!!webAlert}
          title={webAlert.title}
          message={webAlert.message}
          buttons={webAlert.buttons}
          onClose={() => setWebAlert(null)}
        />
      )}
    </SafeAreaProvider>
  );
}

const colors = {
  visaBlue: '#0033a0',
  border: '#ddd',
  text: '#333',
  tabInactive: '#999',
};

const styles = StyleSheet.create({
  container: { flex: 1 },
  content: { flex: 1 },
  tabBar: {
    flexDirection: 'row',
    borderTopWidth: 1,
    borderTopColor: colors.border,
    backgroundColor: '#fff',
  },
  tab: {
    flex: 1,
    paddingVertical: 8,
    alignItems: 'center',
    justifyContent: 'center',
  },
  tabActive: { borderTopWidth: 2, borderTopColor: colors.visaBlue },
  tabText: { fontSize: 11, color: colors.text, marginTop: 4 },
  tabTextActive: { color: colors.visaBlue, fontWeight: '600' },
});

export default App;
